.radioButton{display:inline-block;position:relative;cursor:pointer;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;width:28px;height:28px}.radioButton:hover .buttonShape.background{background-color:#e0e0e0}.radioButton input{position:absolute;opacity:0;cursor:pointer;height:100%;width:100%;margin:0px;left:0;top:0}.radioButton input:checked ~ .checkmark{border-color:#b3b3b3}.radioButton .buttonShape{display:block;position:absolute;border-radius:6px;top:2px;bottom:2px;left:2px;right:2px;margin:-2px}.radioButton .buttonShape.background{background-color:#eee}.radioButton .checkmark{border-color:white;border-width:2px;border-style:solid}.radioButton .checkmark:before{background-color:white}.radioButton .checkmark:after{border-color:white}.radioButton .icon{position:absolute;top:4px;left:4px;height:20px;width:20px}.radioButton .colorfield{position:absolute;top:5px;left:5px;height:18px;width:18px;border-radius:2px}.textureButton{width:80px;height:80px}.textureButton .imageContainer{display:flex;justify-content:center;align-content:center;height:100%;padding:0px 5px}.textureButton .imageContainer img{z-index:100;object-fit:contain;width:100%}
