body{font-family:Arial, Helvetica, sans-serif;font-weight:bold;display:flex;justify-content:center}#container{margin-top:30px;height:400px;max-width:900px;display:flex;flex-direction:row;align-content:center;justify-content:space-between;width:100%}@media screen and (max-width: 900px) and (max-device-width: 900px){#container{flex-direction:column}}td{vertical-align:middle;height:100%;text-align:right}td>div{display:contents}#options{display:inline-flex;flex-direction:column;vertical-align:top;height:100%;width:360px}#options .texture-layer{height:32px;margin:10px}#options .texture-layer .layer-button-group{margin:0px 5px}#options .texture-layer .layer-button-group .layer-button{display:block;color:#8d8d8d}#options .texture-layer .layer-button-group .layer-button :hover{color:#010101;-webkit-transition-duration:0.2s;transition-duration:0.2s;-moz-transition-duration:0.2s}#options .texture-layer .layer-button-group .layer-button>svg{display:block}#options .texture-layer .color-picker{height:100%;width:32px;padding:0px;margin:0px 10px}#options .texture-layer .dropdown{height:100%;width:100px}
